.straws-page {
  padding-top: 46px;
  &__container {
    margin-bottom: 150px;
    @include response(1439px){
      margin-bottom: 90px;
    }
    @include response(1365px){
      margin-bottom: 75px;
    }
    @include response(1023px){
      margin-bottom: 60px;
    }
    @include response(479px){
      margin-bottom: 45px;
    }
  }
  &__holder {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @include response(1023px){
      flex-direction: column;
    }
  }

}

.widget {
  max-width: 410px;
  flex-basis: 410px;
  min-width: 410px;
  width: 100%;
  margin-right: 100px;
  background: $white;
  align-self: stretch;
  @include response(1369px){
    margin-right: 75px;
    max-width: 380px;
    flex-basis: 380px;
    min-width: 380px;
  }
  @include response(1250px){
    margin-right: 45px;
    max-width: 320px;
    flex-basis: 320px;
    min-width: 320px;
  }
  @include response(1023px){
    max-width: 100%;
    flex-basis: auto;
    margin: 0 0 75px;
    min-width: auto;
  }
  &__holder {
    width: 100%;
    height: 100%;
    border-radius: 0 100px 0 100px;
    overflow: hidden;
    box-shadow: 0px 4px 40px rgba(88, 85, 84, 0.1);
    padding: 55px 62px 285px 57px;
    @include response(1250px){
      padding: 55px 35px 185px;
    }
    @include response(1023px){
      padding: 35px 35px 75px;
    }
    @include response(767px){
      padding: 25px 25px 55px;
    }
  }
  &__block {
    padding-bottom: 52px;
    margin-bottom: 55px;
    border-bottom: 1px solid #e0e0e0;
    @include response(1439px){
      margin-bottom: 35px;
      padding-bottom: 35px;
    }
    @include response(1023px){
      margin-bottom: 15px;
      padding-bottom: 15px;
    }
    &:last-child {
      border-bottom: 1px solid transparent;
      margin-bottom: 0;
      padding: 0;
    }
  }
  &__title {
    margin-bottom: 26px;
    @include response(1023px){
      margin-bottom: 20px;
    }
    @include response(767px){
      margin-bottom: 16px;
    }
  }
  &__body {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    .control {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.catalog {
  max-width: calc(100% - 410px);
  width: 100%;
  @include response(1369px){
    max-width: calc(100% - 380px);
  }
  @include response(1250px){
    max-width: calc(100% - 320px);
  }
  @include response(1023px){
    max-width: 100%;
  }
  &__holder {}
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-bottom: 44px;
  }
  &__items {
    max-width: 50%;
    flex-basis: 50%;
    width: 100%;
    padding: 0 10px;
    margin-bottom: 34px;
    @include response(479px){
      max-width: 100%;
      flex-basis: auto;
      padding: 0;
      margin-bottom: 24px;
    }
    &:nth-last-child(-n+2) {
      margin: 0;
      @include response(479px){
        margin-bottom: 24px;
      }
    }
    &:last-child {
      @include response(479px){
        margin-bottom: 0;
      }
    }
    .block-product {
      margin: 0;
      max-width: 100%;
      flex-basis: auto;
      &__holder {
        width: 100%;
      }
      &__preview {
        width: 100%;
      }
      &__img {
        width: 100%;
        > img {
          width: 100%;
        }
      }
    }
  }
}
.picked {
  margin-bottom: 20px;
  &__holder {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @include response(767px){
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &__title {
    margin-right: 24px;
    @include response(767px){
      margin: 0 0 14px;
    }
  }
  &__body {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
  }
  &__reset {
    padding: 4px;
    flex: 1 1 auto;
    &-all {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 5px 14px;
      border: 1px solid #cda97b;
      border-radius: 50px;
      cursor: pointer;
      transition: .2s ease;
      transform: translateY(0);
      opacity: 0.8;
      @include hover() {
        opacity: 1;
      }
    }
  }
  &__list {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
  }
  &__items {
    padding: 4px;
    @include response(767px){
      flex: 1 1 auto;
    }
  }
}

.control {
  margin-bottom: 18px;
  width: 100%;
  @include response(1023px){
    margin-bottom: 16px;
  }
  @include response(767px){
    margin-bottom: 14px;
  }
  &__holder {
    width: 100%;
  }
  &__lb {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;
    position: relative;
    width: 100%;
  }
  &__icon {
    display: inline-block;
    margin-right: 13px;
  }
  &__desc {
    display: inline-block;
    color: $mine;
    text-align: left;
    margin-right: auto;
  }
  &__checkbox {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    width: 0.1px;
    height: 0.1px;
    overflow: hidden;
    &:checked ~ .control__mark{
      background-color: #f9f4f0;
      border: 1px solid #cda97b;
      &::after {
        content: '';
        display: block;
        opacity: 1;
        visibility: visible;
        transform: translate(-50%, -50%) scale(1);
      }
    }
  }
  &__mark {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20px;
    width: 20px;
    border-radius: 3px;
    overflow: hidden;
    background-color: $white;
    border: 1px solid $chicago;
    position: relative;
    cursor: pointer;
    transition: .2s ease;
    &::after {
      content: '';
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      opacity: 0;
      visibility: visible;
      width: 10px;
      height: 10px;
      background-color: #f9f4f0;
      background-image: url(../images/icon-check.svg);
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center center;
      transition: .2s ease;
      transform: scale(0);
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(0);
    }
  }
}

.item-picked {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 14px;
  border: 1px solid rgba(88, 85, 84, 0.2);
  border-radius: 50px;
  cursor: pointer;
  transition: .2s ease;
  transform: translateY(0);
  @include hover() {
    border: 1px solid #cda97b;
    transform: translateY(-4px);
    .item-picked__icon {
      transform: rotate(90deg);
      opacity: 1;
    }
  }
  &__unit {
    margin-right: 9px;
    transition: .2s ease;
  }
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    transform: rotate(0);
    transition: .2s ease;
    opacity: 0.7;
    @include hover() {
      opacity: 1;
    }
  }
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  &__prev,
  &__next {
    @include hover() {
      a {
        svg {
          path {
            fill: #e0e0e0;
            stroke: #e0e0e0;
          }
        }
      }
    }
    a {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &.disabled {
      cursor: default;
      > a {
        cursor: default;
        svg {
          path {
            fill: #e0e0e0;
            stroke: #e0e0e0;
          }
        }
      }
    }
  }
  &__prev {
    margin-right: 40px;
    @include response(479px){
      margin-right: 15px;
    }
  }
  &__next {
    margin-left: 40px;
    @include response(479px){
      margin-left: 15px;
    }
  }
  &__current,
  &__item {
    min-height: 50px;
    max-width: 50px;
    flex-basis: 50px;
    width: 100%;
    margin: 0 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0px 25px 0px 25px;
    box-shadow: 0px 4px 40px rgba(88, 85, 84, 0.1);
    background: $white;
    overflow: hidden;
    @include response(479px){
      min-height: 35px;
      max-width: 35px;
      flex-basis: 35px;
      border-radius: 0px 15px 0px 15px;
    }
    @include hover() {
      span, a {
        color: $niagara;
      }
    }
    span, a {
      display: inline-block;
      height: 50px;
      width: 100%;
      font-size: 14px;
      font-weight: 600;
      line-height: 20px;
      color: $chicago;
      text-transform: uppercase;
      letter-spacing: 0.1em;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: .2s ease;
      @include response(479px){
        height: 35px;
      }
    }
    &--more {
      background: none;
      box-shadow: none;
    }
  }
  &__current {
    background: $niagara;
    @include hover() {
      span, a {
        color: $white;
      }
    }
    span {
      color: $white;
    }
  }
}