.btn {
  font-family: 'Popins', sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: 600;
  letter-spacing: 0.01em;
  color: $white;
  text-transform: uppercase;
  text-decoration: none;
  border-radius: 0;
  outline: none;
  border: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  text-decoration: none;
  padding: 15px 18px 15px 10px;
  cursor: pointer;
  width: 100%;
  transition: .3s ease-in;
  @include response(1250px){
    padding: 12px;
  }
  @include response(767px){
    padding: 10px;
    justify-content: flex-start;
  }
}
.btn-default {
  background: $niagara;
  border-radius: 0 40px;
  @include hover() {
    background: #029b68;
    color: $white;
  }
}
.btn-fill {
  background: transparent;
  color: $mine;
  border-radius: 0 40px 0 0;
  position: relative;
  overflow: hidden;
  transition: all .3s ease;
  &:hover {
    color: $white;
    .btn-fill__icon {
      transform: translateX(4px);
      svg {
        path {
          fill: $white;
        }
      }
    }
    &::after {
      content: '';
      display: block;
      top: 0;
      left: 0;
    }
  }
  &::after {
    content: '';
    display: block;
    position: absolute;
    height: 100%;
    width: 100%;
    border-radius: 0 15px 0 0;
    top: 100%;
    left: -100%;
    background: $niagara;
    transition: all .3s ease;
    z-index: 1;
    transform-origin: 0% 100%;
  }
  &__desc {
    margin-right: 6px;
    z-index: 3;
    letter-spacing: 0.1em;
  }
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateX(0px);
    transition: all .3s ease;
    z-index: 3;
    svg {
      path {
       transition: all .3s ease;
      }
    }
  }
}

.btn-disabled {
  background: #e0e0e0;
  border: 2px solid #e0e0e0;
  color: $white;
  cursor: default;
  &:hover {
    background: #e0e0e0;
    border: 2px solid #e0e0e0;
  }
}