.news-one {
  margin-bottom: 150px;
  @include response(1679px){
    margin-bottom: 120px;
  }
  @include response(1439px){
    margin-bottom: 90px;
  }
  @include response(1365px){
    margin-bottom: 75px;
  }
  @include response(1023px){
    margin-bottom: 60px;
  }
  @include response(479px){
    margin-bottom: 45px;
  }
  .section-articles {
    margin-bottom: 0;
  }
  &__page {
    .breadcrumbs {
      &__link {
        &::after {
          content: '';
          display: block;
          background: $white;
        }
      }
      &__current {
        color: $white;
      }
    }
    .current-page__title {
      > .heading {
        color: $white;
      }
    }
  }
  &__container {
    max-width: 784px;
    margin-bottom: 150px;
    @include response(1679px){
      margin-bottom: 120px;
    }
    @include response(1439px){
      margin-bottom: 90px;
    }
    @include response(1365px){
      margin-bottom: 75px;
    }
    @include response(1023px){
      margin-bottom: 60px;
    }
    @include response(479px){
      margin-bottom: 45px;
    }
  }
  &__holder {}
  &__body {
    margin-bottom: 50px;
    @include response(767px){
      margin-bottom: 30px;
    }
  }
  &__title {
    margin-bottom: 24px;
    text-align: left;
  }
  &__desc {
    > p {
      margin: 0 0 30px;
      @include response(767px){
        margin: 0 0 15px;
      }
      &:last-child {
        margin: 0;
      }
    }
  }
  &__pic {
    margin-bottom: 50px;
  }
  &__frame {
    &-holder {
      margin-bottom: 32px;
      border-radius: 15px;
      overflow: hidden;
      height: 450px;
      @include response(767px){
        height: 350px;
      }
      @include response(479px){
        height: 250px;
      }
      > iframe {
        height: 100%;
      }
    }
    &-row {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &-date {
      color: $mine;
    }
    &-link {
      > a {
        font-size: 14px;
        line-height: 20px;
        font-weight: 600;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        color: $niagara;
      }
    }
  }

}