.popup {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999;
  display: none;
  &.active {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
    z-index: 1;
  }
  &__holder {
    background: #fff;
    padding: 30px;
    position: relative;    
    z-index: 999;
    overflow: hidden;
    &--form {
      max-width: 840px;
      width: 100%;
      padding: 50px 60px;
      @include response(1679px){
        padding: 30px;
      }
      @include response(1439px){
        max-height: 86vh;
        height: 100%;
        overflow: auto;
      }
      @include response(992px){
        max-width: 640px;
      }
      @include response(767px){
        max-width: 100%;
        max-height: 100%;
      }
    }
    &--success {
      max-width: 840px;
      width: 100%;
      padding: 110px 30px;
      @include response(1439px){
        padding: 70px 30px;
      }
      @include response(1365px){
        padding: 50px 30px;
      }
      @include response(1250px){
        padding: 30px;
      }
    }
  }
  &__close {
    position: absolute;
    right: 15px;
    top: 15px;
    cursor: pointer;
    svg {
      width: 15px;
      height: 15px;
      fill: #000;
    }
  }
  &__content {
    &--form {
      .form {
        &__holder {}
        &__section {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          align-items: normal;
        }
        &__title {
          text-align: center;
        }
        &__row {
          @include response(479px){
            flex-direction: column;
            margin: 0 0 20px;
            flex-wrap: wrap;
          }
        }
        &__cell {
          @include response(479px){
            max-width: 100%;
            flex-basis: auto;
            margin: 0 0 15px;
            &:last-child {
              margin: 0;
            }
          }
        }
        &__box {
          &--comment {
            @include response(1679px){
              height: 130px;
            }
            @include response(1439px){
              height: 100px;
            }
          }
          &.toggle-comment {
            @include response(1679px){
              height: 0;
            }
          }
        }
        &__add {
          display: flex;
          justify-content: center;
          align-items: center;
          @include response(1679px){
            margin-bottom: 35px;
          }
        }
        &__button {
          margin: 0 auto;
        }
        .field-control {
          @include response(1679px){
            height: 55px;
            padding: 14px 20px;
          }
          @include response(1439px){
            height: 50px;
          }
          &-area {
            @include response(1679px){
              height: 100px;
            }
            @include response(1439px){
              height: 70px;
            }
          }
        }
      }
    }
  }
}

.success {
  &__holder {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  &__icon {
    margin-bottom: 35px;
    text-align: center;
  }
  &__thx {
    margin-bottom: 17px;
    text-align: center;
  }
  &__desc {
    text-align: center;
    max-width: 370px;
  }
}


.iosBugFixCaret {
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: hidden;
}