body {
  height: 100%;
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 400;
  color: $mine;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: none;
}
* {
  outline: none;
  box-sizing: border-box;
  &::after,
  &::before {
    box-sizing: border-box;
  }
}
svg {
  transform: translateZ(0);
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: border-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
input[type="submit"] {
  cursor: pointer;
}
h1 {
  font-size: 34px;
  line-height: 36px;
  font-weight: 700;
}
h2 {
  font-size: 24px;
  line-height: 26px;
  font-weight: normal;
}
h3 {
  font-size: 22px;
  line-height: 24px;
  font-weight: normal;
}
h4 {
  font-size: 16px;
  line-height: 18px;
  font-weight: normal;
}
h5 {
  font-size: 12px;
  line-height: 14px;
  font-weight: normal;
}
h6 {
  font-size: 10px;
  line-height: 12px;
  font-weight: normal;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
a {
  text-decoration: none;
  display: block;
}
p.error {
  font-size: 12px;
  line-height: 14px;
  color: red;
}
.wrapper {
  overflow: hidden;
  min-height: 100vh;
  width: 100%;
  position: relative;
}
.holder-block {
  @include response(1023px) {
    overflow: hidden;
    position: relative;
    height: 100%;
  }
}
// Common styles
.container {
  max-width: 1460px;
  width: 100%;
  margin: 0 auto;
  @include response(1679px){
    padding: 0 15px;
  }
  @include response(1023px){
    padding: 0 10px;
  }
}

.main {
  &__holder {}
}

.offset {
  padding-top: 138px;
  @include response(1439px){
    padding-top: 75px;
  }
  @include response(767px){
    padding-top: 55px;
  }
}

// Text styles
.heading {
  font-size: 65px;
  line-height: 97px;
  font-weight: 700;
  color: $mine;
  text-align: left;
  @include response(1439px){
    font-size: 56px;
    line-height: 64px;
  }
  @include response(1250px){
    font-size: 48px;
    line-height: 54px;
  }
  @include response(767px){
    font-size: 42px;
    line-height: 48px;
  }
  @include response(479px){
    font-size: 36px;
    line-height: 40px;
  }
  @include response(359px){
    font-size: 32px;
    line-height: 40px;
  }
} 
.caption {
  font-size: 40px;
  line-height: 46px;
  font-weight: 700;
  color: $mine;
  text-align: left;
  @include response(1439px){
    font-size: 36px;
    line-height: 40px;
  }
  @include response(1023px){
    font-size: 32px;
    line-height: 38px;
    margin-bottom: 12px;
  }
  @include response(767px){
    font-size: 28px;
    line-height: 32px;
  }
  @include response(479px){
    font-size: 26px;
    line-height: 30px;
  }
}
.caption-sm {
  font-size: 30px;
  line-height: 46px;
  font-weight: bold;
  color: $mine;
  @include response(1023px){
    font-size: 28px;
    line-height: 32px;
  }
  @include response(767px){
    font-size: 26px;
    line-height: 30px;
  }
}
.text-lg {
  font-size: 22px;
  line-height: 30px;
  font-weight: 700;
  color: $mine;
  @include response(767px){
    font-size: 20px;
    line-height: 26px;
  }
  @include response(479px){
    font-size: 18px;
    line-height: 22px;
  }
}
.text-md {
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  color: $chicago;
}
.text-sm {
  font-size: 14px;
  line-height: 22px;
  font-weight: 500;
  color: $chicago;
}
.text-xs {
  font-size: 12px;
  line-height: 20px;
  font-weight: 500;
  color: $chicago;
}

.bold {
  font-weight: 600;
}
// Sliders styles
.slide-current {
  font-size: 26px; 
  line-height: 40px;
  @include response(767px){
    font-size: 22px;
    line-height: 28px;
  }
}
.slide-all {
  font-size: 14px;
  line-height: 20px;
}
.main-arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  @include hover() {
    svg {
      path {
        fill: $niagara;
        stroke: $niagara;
      }
    }
  }
  > svg {
    path {
      transition: opacity .3s ease-in-out;
    }
  }
}
.slick-dots {
  position: absolute;
  padding: 0;
  margin: 0 -3px;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: -72px;
  left: 50%;
  transform: translate(-50%, 0);
  > li {
    border: 2px solid #c4c4c4;
    height: 10px;
    width: 10px;
    border-radius: 50%;
    color: transparent;
    background: #c4c4c4;
    cursor: pointer;
    position: relative;
    margin: 0 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    transform-origin: 50% 50%;
    transform: scale(1);
    transition: .3s ease-in-out;
    > button {
      height: 100%;
      width: 100%;
      border: none;
      background: #c4c4c4;
      color: transparent;
      cursor: pointer;
    }
    &.slick-active {
      border: 2px solid $niagara;
      transform: scale(1.1);
      > button {
        cursor: default;
        background: $niagara;
      }
    }
  }
}

//Buttons 
.button-holder {
  max-width: 220px;
  text-align: left;
  border-bottom: 2px solid $niagara;
  flex-basis: auto;
  width: auto;
  display: inline-block;
}

//Section titles
.section-title {
  &__holder {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
  &__desc {}
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    @include hover() {
      .section-title__text {
        opacity: 1;
      }
      .section-title__icon {
        transform: translateX(5px);
      }
    }
  }
  &__text {
    font-size: 14px;
    line-height: 20px;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    color: $niagara;
    margin-right: 12px;
    opacity: 0.75;
    transition: .3s ease-in-out;
  }
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateX(0);
    transition: .3s ease-in-out;
  }
}

// Label styles
.label-r {
  overflow: hidden;
  min-width: 133px;
  width: 100%;
  &__holder {
    background: $niagara;
    border-radius: 40px 0 0 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__holder-reverse {
    border-radius: 0 0 40px 0;
  }
  &__text {
    display: inline-block;
    font-size: 12px;
    line-height: 18px;
    font-weight: 600;
    letter-spacing: 0.1em;
    color: $white;
    text-align: center;
    padding: 8px;
    width: 100%;
    text-align: center;
  }
}

//Tabs styles
.tabs-list {
  display: flex;
  justify-content: center;
  align-items: center;
}
.tabs-items {}
.tabs-link {
  font-weight: 600;
  font-size: 14px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: $mine;
  padding: 0 30px;
  position: relative;
  @include response(479px){
    padding: 0 30px 0 0;
    padding: 30px;
  }
  &::after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    bottom: -10px;
    border-bottom: 2px solid #e0e0e0;
    transition: all .3s ease-in-out;
  }
  &.active {
    &::after {
      content: '';
      display: block;
      border-bottom: 2px solid $niagara;
    }
  }
}

// Block product styles
.block-product {
  max-width: 440px;
  flex-basis: 440px;
  width: 100%;
  box-shadow: 0 0 0 rgba(255, 255, 255, 0);
  transition: .3s ease-in-out;
  margin: 0 auto 35px 0;
  @include hover() {
    box-shadow: 0 4px 40px rgba(0, 0, 0, 0.05);
    .caption-sm {
      opacity: 1;
    }
  }
  &__holder {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__preview {
    position: relative;
    display: inline-block;
  }
  &__img {}
  &__label {
    position: absolute;
    bottom: 0;
    right: 0;
    .label-r {
      &__text {
        padding: 8px 4px 8px 18px;
      }
    }
  }
  &__body {
    background: $white;
    padding: 22px 26px 34px 20px;
    width: 100%;
    min-height: 300px;
    height: 100%;
    @include response(1439px){
      padding: 18px;
    }
    @include response(1365px){
      min-height: 270px;
    }
    @include response(479px){
    min-height: auto;
    }
    &-holder {
      width: 100%;
      .button-holder {
        .btn {
          @include response(1023px){
            padding: 8px;
          }
        }
      }
    }
    &-link {
      margin-bottom: 20px;
      min-height: 90px;
      .caption-sm {
        opacity: 0.8;
        transition: .3s ease-in-out;
        @include hover() {
          opacity: 1;
        }
      }
    }
    &-desc {
      margin-bottom: 36px;
      max-width: 90%;
      flex-basis: 90%;
      width: 100%;
      text-align: left;
      min-height: 90px;
      @include response(1023px){
        margin-bottom: 24px;
      }
    }
  }
}

// Box img styles
.box-img {
  max-width: 350px;
  flex-basis: 350px;
  width: 100%;
  margin-bottom: 20px;
  @include response(1679px){
    padding: 5px;
  }
  @include response(1439px){
    max-width: 320px;
    flex-basis: 320px;
  }
  @include response(1365px){
    max-width: 300px;
    flex-basis: 300px;
  }
  @include response(1250px){
    max-width: 33.3333%;
    flex-basis: 33.3333%;
    margin-bottom: 10px;
    padding: 0 5px;
  }
  @include response(1023px){
    max-width: 50%;
    flex-basis: auto;
  }
  &__holder {
    position: relative;
    border-radius: 15px 15px 15px 0;
    overflow: hidden;
    @include response(1250px){
      width: 100%;
    }
  }
  &__img {
    overflow: hidden;
    @include response(1250px){
      width: 100%;
      > img {
        width: 100%;
      }
    }
  }
  &__label {
    position: absolute;
    bottom: 0px;
    right: 0px;
  }
}

// Box articles styles
.box-articles {
  max-width: 33.3333%;
  flex-basis: 33.3333%;
  width: 100%;
  padding: 0 10px;
  @include response(1023px){
    max-width: 50%;
    flex-basis: 50%;
    padding: 0 5px;
    margin-bottom: 35px;
  }
  @include response(767px){
    max-width: 100%;
    flex-basis: auto;
    &:last-child {
      margin: 0;
    }
  }
  &__holder {
    width: 100%;
  }
  &__pic {
    position: relative;
    border-radius: 0 15px 15px 15px;
    overflow: hidden;
    margin-bottom: 18px;
    width: 100%;
    @include hover() {
      .box-articles__link {
        &::after {
          content: '';
          display: block;
          opacity: 1;
        }
      }
    }
  }
  &__link {
    z-index: 3;
    position: relative;
    width: 100%;
    > img {
      width: 100%;
    }
    &::after {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.3);
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      opacity: 0;
      transition: .3s ease-in-out;
    }
  }
  &__label {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
  }
  &__body {
    .button-holder {
      max-width: 200px;
      flex-basis: 200px;
      width: 100%;
    }
  }
  &__desc {
    margin-bottom: 15px;
  }
  &__title {
    text-align: left;
    margin-bottom: 10px;
    transition: opacity .3s ease-in-out;
    opacity: 0.85;
    min-height: 60px;
    height: 100%;
    @include hover() {
      opacity: 1;
    }
  }
  &__text {
    text-align: left;
  }
}

// Scroll block styles
.scroll {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  &__container {}
  &__holder {
    padding: 120px 0;
    @include response(1439px){
      padding: 100px 0;
    }
    @include response(1365px){
      padding: 75px 0;
    }
    @include response(1023px){
      padding: 55px 0;
    }
    @include response(767px){
      padding: 45px 0;
    }
    @include response(479px){
      padding: 35px 0;
    }
  }
  &__body {
    max-width: 785px;
    flex-basis: 785px;
    width: 100%;
    max-height: 250px;
    margin-left: auto;
  }
  &__title {
    text-align: left;
    margin-bottom: 24px;
  }
  &__desc {
    margin-right: 100px;
    @include response(1250px){
      margin-right: 30px;
    }
    @include response(767px){
      margin: 0;
    }
    p {
      margin: 0 0 30px;
      @include response(767px){
        margin: 0 0 15px;
      }
      &:last-child {
        margin: 0;
      }
    }
  }
}

// Current page styles
.current-page {
  padding: 150px 15px;
  margin: 100px 0;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  @include response(1439px){
    padding: 120px 15px;
  }
  @include response(1365px){
    padding: 100px 15px;
  }
  @include response(1250px){
    padding: 75px 15px;
  }
  @include response(1023px){
    padding: 55px 15px;
    margin: 74px 0 55px;
  }
  @include response(767px){
    padding: 45px 15px;
  }
  @include response(479px){
    padding: 35px 15px;
  }
  &__container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__holder {}
  &__title {
    .heading {
      text-align: center;
    }
  }
}

