.footer {
  padding: 64px 0 60px;
  box-shadow: 0px 4px 40px rgba(88, 85, 84, 0.1);
  @include response(1439px){
    padding: 54px 0 50px;
  }
  @include response(1365px){
    padding: 44px 0;
  }
  @include response(767px){
    padding: 35px 0;
  }
  @include response(479px){
    padding: 25px 0;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @include response(1439px){
      flex-wrap: wrap;
    }
  }
  &__logo {
    @include response(1439px){
      max-width: 100%;
      width: 100%;
      display: flex;
      justify-content: center;
      margin-bottom: 35px;
    }
    @include response(1023px){
      margin-bottom: 26px;
    }
    @include response(767px){
      margin-bottom: 15px;
    }
    &-holder {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      @include response(1439px){
        align-items: center;
      }
      .logo {
        margin: 0 0 45px;
        flex-basis: auto;
        @include response(1439px){
          margin: 0 0 15px;
          max-width: 145px;
        }
        @include response(1250px){
          max-width: 135px;
        }
        @include response(1023px){
          max-width: 125px;
        }
      }
    }
  }
  &__menu {
    @include response(1439px){
      max-width: 100%;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 35px;
    }
    @include response(1023px){
      margin-bottom: 26px;
    }
    @include response(767px){
      margin-bottom: 15px;
    }
    &-holder {
      .nav {
        @include response(1439px){
          margin: 0;
        }
        &__menu {
          width: 400px;
          flex-wrap: wrap;
          @include response(1439px){
            width: auto;
            flex-wrap: nowrap;
          }
          @include response(1023px){
            flex-direction: row;
          }
          @include response(767px){
            flex-direction: column;
          }
          li {
            margin-bottom: 30px;
            padding: 0;
            max-width: 50%;
            width: 100%;
            @include response(1439px){
              margin: 0 15px;
              max-width: 100%;
              width: auto;
            }
            @include response(767px){
              margin: 0 0 10px;
            }
            &:nth-last-child(-n+2) {
              margin: 0;
              @include response(1439px){
                margin: 0 15px;
              }
              @include response(1023px){
                padding: 0;
              }
              @include response(767px){
                margin: 0 0 10px;
              }
            }
            a {
              font-size: 14px;
              line-height: 20px;
              font-weight: 500;
              color: $mine;
              text-align: left;
              text-transform: none;
              padding: 0;
              @include response(1439px){
                text-align: center;
                font-size: 16px;
              }
            }
          }
        }
      }
    }
  }
  &__contacts {
    @include response(1439px){
      max-width: 100%;
      width: 100%;
      margin-bottom: 25px;
    }
    &-holder {}
  }
  &__soc {
    @include response(1439px){
      max-width: 100%;
      width: 100%;
    }
    &-holder {
      @include response(1439px){
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
    }
  }
}

.copyright {
  &__holder {
    font-size: 12px;
    line-height: 20px;
    color: $mine;
    text-align: left;
  }
}

.contacts {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  @include response(1439px){
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap
  }
  &__items {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 16px;
    @include response(1439px){
      margin: 8px 15px;
    }
    &:last-child {
      margin: 0;
      @include response(1439px){
        margin: 8px 15px;
      }
    }
  }
  &__icon {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 14px;
    background: #f3f1ed;
  }
  &__link {
    transition: .2s ease-in-out;
    font-style: normal;
    @include hover() {
      color: $niagara;
    }
  }
}

.soc {
  margin-bottom: 45px;
  @include response(1439px){
    margin-bottom: 15px;
  }
  &__title {
    margin-bottom: 5px;
    text-align: left;
    @include response(1439px){
      text-align: center;
    }
  }
  &__list {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 0 -5px;
    @include response(1439px){
      justify-content: center;
    }
  }
  &__items {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    overflow: hidden;
    background: $chicago;
    margin: 0 5px;
    @include hover() {
      .soc__link {
        background: $niagara;
      }
    }
  }
  &__link {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: .2s ease-in-out;
  }
}
