.product-page {
  &__holder {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-bottom: 150px;
    @include response(1439px){
      margin-bottom: 120px;
    }
    @include response(1365px){
      margin-bottom: 100px;
    }
    @include response(1250px){
      margin-bottom: 75px;
    }
    @include response(1023px){
      flex-direction: column;
      align-items: center;
      margin-bottom: 55px;
    }
  }
  .item-product {
    .button-holder {
      @include response(479px){
        max-width: 160px;
      }
      .btn {
        @include response(479px){
          font-size: 12px;
        }
      }
    }
  }
}

.item-product {
  max-width: 50%;
  flex-basis: 50%;
  width: 100%;
  @include response(1024px){
    max-width: 70%;
    flex-basis: 70%;
    margin-bottom: 25px;
    &:last-child {
      margin: 0;
    }
  }
  @include response(767px){
    max-width: 90%;
    flex-basis: 90%;
  }
  &__holder {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    @include response(640px){
      flex-direction: column;
    }
  }
  &__pic {
    max-width: 360px;
    flex-basis: 360px;
    width: 100%;
    @include response(1250px){
      max-width: 50%;
      flex-basis: 50%;
    }
    @include response(640px){
      flex-basis: auto;
      max-width: 100%;
    }
  }
  &__img {
    border-radius: 150px 0 0 15px;
    overflow: hidden;
    @include response(640px){
      max-height: 300px;
      border-radius: 150px 0 0 0;
    }
    > img {
      @include response(640px){
        width: 100%; 
      }
    }
  }
  &__box {
    border-radius: 0 15px 150px 0;
    overflow: hidden;
    box-shadow: 0px 4px 40px rgba(0, 0, 0, 0.05);
    max-width: 360px;
    flex-basis: 360px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    align-self: stretch;
    padding: 0 40px;
    @include response(1250px){
      padding: 0 20px;
    }
    @include response(640px){
      max-width: 100%;
      flex-basis: auto;
      padding: 20px;
      border-radius: 0 0 150px 0;
    }
    .button-holder {
      max-width: 180px;
      flex-basis: auto;
      width: 100%;
    }
  }
  &__info {
    margin-bottom: 40px;
    @include response(1250px){
      margin-bottom: 32px;
    }
  }
  &__title {
    margin-bottom: 20px;
    @include response(1250px){
      margin-bottom: 15px;
    }
    .caption {
      @include response(1250px){
        font-size: 32px;
        line-height: 36px;
      }
    }
  }
  &__desc {
    width: 100%;
    max-width: 280px;
    min-height: 90px;
  }
}