.slideshow {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  padding-bottom: 226px;
  @include response(1679px){
    padding-bottom: 120px;
  }
  @include response(1250px){
    padding-bottom: 100px;
  }
  @include response(1023px){
    padding-bottom: 75px;
  }
  @include response(767px){
    padding-bottom: 55px;
  }
  @include response(479px){
    padding-bottom: 45px;
  }
  &__holder {
    margin: 174px auto 0;
    @include response(1679px){
      margin: 124px auto 0;
    }
    @include response(1439px){
      margin: 95px auto 0;
    }
    @include response(1023px){
      margin: 55px auto 0;
    }
    @include response(767px){
      margin: 35px auto 0;
    }
  }
  &__nav {
    margin-bottom: 14px;
    position: relative;
  }
  &__arrows {
    position: absolute;
    top: 50%;
    left: 0;
    max-width: 110px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transform: translateY(-50%);
  }
  &__count {
    font-weight: 600;
    color: $mine;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    padding-left: 25px;
  }
  &__slider {}
  &__items {}
  &__block {
    max-width: 700px;
    flex-basis: 700px;
    width: 100%;
  }
  &__body {
    margin-bottom: 50px;
    @include response(1439px){
      margin-bottom: 36px;
    }
    @include response(767px){
      margin-bottom: 24px;
    }
  }
  &__button {
    max-width: 160px;
    flex-basis: 160px;
    width: 100%;
    text-align: left;
  }
  &__title {
    margin-bottom: 12px;
  }
  &__text {
    font-size: 18px;
    line-height: 28px;
    font-weight: 500;
    color: $chicago;
  }
}

.application {
  box-shadow: 0 4px 40px rgba(0, 0, 0, 0.05);
  margin-bottom: 120px;
  @include response(1439px){
    margin-bottom: 90px;
  }
  @include response(1365px){
    margin-bottom: 75px;
  }
  @include response(1023px){
    margin-bottom: 60px;
  }
  @include response(479px){
    margin-bottom: 45px;
  }
  &__container {
    padding: 69px 0 88px;
    @include response(1679px){
      padding: 69px 15px 88px;
    }
    @include response(1439px){
      padding: 60px 15px 68px;
    }
    @include response(1365px){
      padding: 45px 15px 58px;
    }
    @include response(1023px){
      padding: 35px 10px 48px;
    }
    @include response(767px){
      padding: 25px 10px;
    }
  }
  &__title {
    margin-bottom: 75px;
    @include response(1439px){
      margin-bottom: 55px;
    }
    @include response(1250px){
      margin-bottom: 35px;
    }
  }
  &__holder {
    position: relative;
  }
  &__arrows {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    width: 100%;
    top: 50%;
    left: 0;
    right: 0;
    transform: translateY(-50%);
    z-index: 30;
    .main-arrow {
      position: absolute;
      transform: translateX(50%);
      z-index: 30;
      @include response(1679px){
        transform: translateX(0);
      }
      @include response(767px){
        width: 12px;
      }
      @include response(479px){
        width: 10px;
      }
    }
    .prev-arrow {
      left: -85px;
      @include response(1679px){
        left: 0%
      }
    }
    .next-arrow {
      right: -85px;
      @include response(1679px){
        right: 0%
      }
    }
  }
  &__slider {}
  &__items {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__icons {
    margin-bottom: 30px;
    position: relative;
    text-align: center;
    min-height: 90px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    @include response(1439px){
      margin-bottom: 15px;
    }
    > span {
      transition: .2s ease;
    }
    &:hover {
      .application__visible {
        display: none;
      }
      .application__hidden {
        display: block;
      }
    }
  }
  &__visible {
    display: inline-block;
  }
  &__hidden {
    display: inline-block;
    position: absolute;
    display: none;
    z-index: 10;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  &__desc {
    text-align: center;
  }
  &__text {
    text-align: center;
  }
}

.production {
  margin-bottom: 135px;
  @include response(1439px){
    margin-bottom: 115px;
  }
  @include response(1365px){
    margin-bottom: 95px;
  }
  @include response(1250px){
    margin-bottom: 75px;
  }
  @include response(1023px){
    margin-bottom: 55px;
  }
  @include response(767px){
    margin-bottom: 45px;
  }
  @include response(479px){
    margin-bottom: 25px;
  }
  &__container {
    .caption {
      margin-bottom: 60px;
      @include response(1439px){
        margin-bottom: 48px;
      }
      @include response(1365px){
        margin-bottom: 32px;
      }
      @include response(1250px){
        margin-bottom: 28px;
      }
      @include response(1023px){
        margin-bottom: 24px;
      }
    }
    .section-title {
      margin-bottom: 34px;
    }
  }
  &__items {
    @include response(1439px){
      padding: 0 10px;
    }
    @include response(1250px){
      padding: 0 5px;
    }
    .block-product {
      @include response(1439px){
        margin: 0 auto 35px;
        max-width: 400px;
        flex-basis: 400px;
      }
      @include response(1250px){
        max-width: 350px;
        flex-basis: 350px;
      }
      @include response(479px){
        max-width: 95%;
        flex-basis: auto;
      }
    }
  }
  &__holder {}
  &__tabs {
    @include response(479px){
      margin: 0 15px 35px 0;
    }
    &-content {
      position: relative;
    }
  }
  &__arrows {
    position: absolute;
    width: 100%;
    z-index: 30;
    display: flex;
    justify-content: space-between;
    align-items: center;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    .main-arrow {
      position: absolute;
      transform: translateX(50%);
      @include response(1679px){
        transform: translateX(0);
      }
      @include response(767px){
        width: 12px;
      }
      @include response(479px){
        width: 10px;
      }
    }
    .prev-arrow {
      left: -85px;
      @include response(1679px){
        left: -50px;
      }
      @include response(1439px){
        left: -20px;
        transform: translateX(10px);
      }
      @include response(1365px){
        left: -15px;
      }
    }
    .next-arrow {
      right: -85px;
      @include response(1679px){
        right: -20px;
      }
      @include response(1439px){
        right: -10px;
        transform: translateX(5px);
      }
      @include response(1365px){
        right: -5px;
      }
    }
  }
  &__slider {
    .slick-dots {
      bottom: 0;
    }
  }
}

