.section-tabs {
  margin-bottom: 130px;
  @include response(1439px){
    margin-bottom: 90px;
  }
  @include response(1365px){
    margin-bottom: 75px;
  }
  @include response(1023px){
    margin-bottom: 60px;
  }
  @include response(479px){
    margin-bottom: 45px;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @include response(1250px){
      flex-direction: column;
      align-items: center;
    }
  }
  &__tabs {
    padding-right: 20px;
    @include response(1250px){
      padding: 0;
      margin-bottom: 25px;
    }
    .tabs-list {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      @include response(1250px){
        flex-direction: row;
      }
    }
    .tabs-items {
      border-radius: 160px 15px 15px;
      overflow: hidden;
      box-shadow: 0 4px 40px rgba(0, 0, 0, 0.05);
      max-width: 348px;
      min-width: 348px;
      flex-basis: 100%;
      width: 100%;
      min-height: 314px;
      height: 100%;
      background: $white;
      margin-bottom: 20px;

      display: flex;
      flex-direction: column;
      @include response(1439px){
        max-width: 318px;
        min-width: 318px;
        min-height: 288px;
      }
      @include response(1365px){
        max-width: 300px;
        min-width: 300px;
        min-height: 270px;
      }
      @include response(1250px){
        margin: 0;
        max-width: 250px;
        min-width: 250px;
        min-height: 250px;
      }
      @include response(1023px){
        max-width: 230px;
        min-width: 230px;
        min-height: 200px;
      }
      @include response(767px){
        min-height: 150px;
      }
      @include response(479px){
        max-width: 150px;
        min-width: 150px;
        min-height: 100px;
      }
      &:first-child {
        .active {
          background: $niagara;
        }
      }
      &:last-child {
        margin-bottom: 0;
        border-radius: 15px 15px 15px 160px;
        @include response(1250px){
          border-radius: 160px 15px 15px;
        }
        .active {
          background: #cda97b;
        }
      }
    }
    .tabs-link {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      padding: 15px;
      color: $mine;
      font-size: 30px;
      line-height: 46px;
      font-weight: bold;
      margin-bottom: 8px;
      text-transform: none;
      letter-spacing: normal;
      max-width: 348px;
      flex-basis: 100%;
      width: 100%;
      min-height: 314px;
      height: 100%;
      transition: .2s ease-in-out;
      margin-bottom: 0;
      padding-left: 95px;
      @include response(1439px){
        padding-left: 55px;
        min-height: 288px;
      }
      @include response(1365px){
        min-height: 270px;
      }
      @include response(1250px){
        min-height: 250px;
      }
      @include response(1023px){
        font-size: 24px;
        line-height: 30px;
        min-height: 200px;
        padding-left: 35px;
      }
      @include response(767px){
        min-height: 150px;
        font-size: 18px;
        line-height: 24px;
      }
      @include response(479px){
        min-height: 100px;
        font-size: 16px;
        line-height: 20px;
      }
      > span {
        font-size: 16px;
        line-height: 20px;
        font-weight: 500;
        color: $mine;
        text-align: center;
        @include response(479px){
          font-size: 14px;
          line-height: 18px;
        }
      }
      > p {
        margin: 0 0 8px 0;
        width: 100%;
        text-align: left;
        @include response(1250px){
          text-align: center;
        }
      }
      &::after {
        content: '';
        display: block;
        border-bottom: 0;
      }
      &.active {
        color: $white;
        > span {
          color: $white;
        }
      }
    }
  }
  &__content {
    width: 100%;
    max-width: 100%;
  }
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    width: 100%;
    max-width: 100%;
    @include response(1439px){
      justify-content: flex-start;
    }
  }
}

