.about-page {
  &__container {}
}

.triggers {
  margin-bottom: 150px;
  @include response(1439px){
    margin-bottom: 90px;
  }
  @include response(1365px){
    margin-bottom: 75px;
  }
  @include response(1023px){
    margin-bottom: 60px;
  }
  @include response(479px){
    margin-bottom: 45px;
  }
  &__row {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: row;
    margin-bottom: 100px;
    @include response(1250px){
      margin-bottom: 75px;
    }
    @include response(1023px){
      margin-bottom: 55px;
    }
    @include response(767px){
      flex-direction: column;
    }
    @include response(479px){
      margin-bottom: 45px;
    }
    &-reverse {
      flex-direction: row-reverse;
      @include response(767px){
        flex-direction: column;
      }
      .triggers__pic {
        margin: 0 0 0 88px;
        border-radius: 150px 0 15px 15px;
        @include response(767px){
          margin: 0 0 35px 0;
        }
        @include response(479px){
          border-radius: 70px 0 15px 15px;
        }
      }
    }
  }
  &__pic {
    margin: 0 88px 0 0;
    max-width: 727px;
    flex-basis: 727px;
    min-width: 727px;
    width: 100%;
    border-radius: 0 150px 15px 150px;
    overflow: hidden;
    @include response(1439px){
      max-width: 50%;
      flex-basis: auto;
      min-width: 50%;
    }
    @include response(1250px){
      margin: 0 68px 0 0;
    }
    @include response(1023px){
      margin: 0 48px 0 0;
    }
    @include response(767px){
      margin: 0 0 35px 0;
      max-width: 90%;
      min-width: 90%;
    }
    @include response(479px){
      border-radius: 0 70px 15px 70px;
    }
  }
  &__img {
    > img {
      display: block;
      width: 100%;
    }
  }
  &__body {
    max-width: 490px;
    flex-basis: 490px;
    width: 100%;
    @include response(767px){
      flex-basis: auto;
      max-width: 90%;
    }
  }
  &__title {
    margin-bottom: 36px;
    position: relative;
    @include response(1250px){
      margin-bottom: 28px;
    }
    @include response(479px){
      margin-bottom: 22px;
    }
    .caption {
      text-align: left;
    }
  }
  &__count {
    width: 135px;
    height: 160px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    position: absolute;
    bottom: -50%;
    left: -15%;
    transform: translate(0, 0);
    @include response(1439px){
      width: 115px;
      height: 140px;
    }
    @include response(767px){
      width: 95px;
      height: 120px;
    }
  }
  &__desc {
    width: 100%;
    text-align: left;
    > p {
      margin: 0 0 30px;
      @include response(767px){
        margin: 0 0 15px;
      }
      &:last-child {
        margin: 0;
      }
    }
  }
  &__list {}
  &__items {
    margin-bottom: 10px;
    position: relative;
    padding-left: 26px;
    &::after {
      content: '';
      display: block;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: $niagara;
      position: absolute;
      top: 5px;
      left: 0;
    }
    &:last-child {
      margin: 0;
    }
  }
}

.certificates {
  margin-bottom: 230px;
  @include response(1679px){
    margin-bottom: 200px;
  }
  @include response(1439px){
    margin-bottom: 175px;
  }
  @include response(767px){
    margin-bottom: 125px;
  }
  &__holder {}
  &__body {
    .section-title {
      margin-bottom: 70px;
      @include response(1439px){
        margin-bottom: 55px;
      }
      @include response(1250px){
        margin-bottom: 35px;
      }
      &__holder {
        @include response(479px){
          flex-direction: column;
        }
      }
    }
  }
  &__arrows {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 80px;
    width: 100%;
    .main-arrow {
      > svg {
        path {
          transition: .2s ease-in-out;
        }
      }
      @include hover() {
        > svg {
          path {
            fill: $niagara;
          }
        }
      }
    }
  }
  &__list  {}
  &__items {}
  &__block {}
  &__modal {
    margin-bottom: 32px;
    border-radius: 15px 0 15px 0;
    overflow: hidden;
  }
  &__link {
    position: relative;
    @include hover() {
      &::after,
      &::before {
        content: '';
        display: block;
        opacity: 1;
        visibility: visible;
      }
    }
    &::after,
    &::before {
      content: '';
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: .2s ease;
      cursor: pointer;
      opacity: 0;
      visibility: hidden;
    }
    &::after {
      content: '';
      width: 60px;
      height: 60px;
      background-color: $niagara;
      border-radius: 0px 25px 0 25px;
      z-index: 3;
    }
    &::before {
      content: '';
      background-image: url(../images/icon-zoom.svg);
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      height: 30px;
      width: 30px;
      z-index: 6;
    }
  }
  &__desc {
    color: $mine;
    text-align: center;
    padding: 0 10px;
  }
}

.block-team {
  margin-bottom: 235px;
  @include response(1679px){
    margin-bottom: 200px;
  }
  @include response(1439px){
    margin-bottom: 175px;
  }
  @include response(767px){
    margin-bottom: 125px;
  }
  &__holder {
    .section-title {
      margin-bottom: 70px;
      @include response(1439px){
        margin-bottom: 55px;
      }
      @include response(1250px){
        margin-bottom: 35px;
      }
      &__holder {
        @include response(479px){
          flex-direction: column;
        }
      }
    }
  }
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 70px;
  }
  &__arrows {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 80px;
    width: 100%;
    .main-arrow {
      > svg {
        path {
          transition: .2s ease-in-out;
        }
      }
      @include hover() {
        > svg {
          path {
            fill: $niagara;
          }
        }
      }
    }
  }
  &__list {}
  &__carouesl {
    position: relative;
  }
}

.team {
  max-width: 473px;
  flex-basis: 473px;
  width: 100%;
  padding: 0 10px;
  @include response(640px){
    max-width: 100%;
    flex-basis: auto;
  }
  &__holder {
    box-shadow: 0 4px 40px rgba(0, 0, 0, 0.02);
    border-radius: 15px 0 15px 0;
    overflow: hidden;
    min-height: 515px;
    height: 100%;
    position: relative;
    @include response(1250px){
      min-height: 450px;
    }
    @include hover() {
      .team__info {
        top: 45%;
      }
      .team__pic {
        &::after {
          content: '';
          display: block;
          opacity: 1;
          visibility: visible;
        }
      }
    }
  }
  &__pic {
    position: relative;
    > img {
      @include response(680px){
        width: 100%;
      }
    }
    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      opacity: 0;
      visibility: hidden;
      left: 0;
      background: rgba(0, 0, 0, 0.35);
      transition: opacity .3s cubic-bezier(0.35, 0.19, 0.46, 0.83), visibility .3s cubic-bezier(0.35, 0.19, 0.46, 0.83);
    }
  }
  &__info {
    background: $white;
    position: absolute;
    top: 80%;
    width: 100%;
    right: 0;
    transition: top .3s cubic-bezier(0.35, 0.19, 0.46, 0.83);
    @include response(1250px){
      top: 75%;
    }
    @include response(1023px){
      top: 45%;
    }
    @include response(640px){
      top: 50%;
    }
  }
  &__inner {
    background: $white;
    padding: 28px 40px;
    min-height: 270px;
    height: 100%;
    @include response(1024px){
      padding: 25px;
    }
  }
  &__title {
    margin-bottom: 26px;
    text-align: left;
  }
  &__name {
    margin-bottom: 8px;
  }
  &__postition {}
  &__contacts {
    margin-bottom: 14px;
  }
  &__tel {
    margin-bottom: 8px;
    text-align: left;
  }
  &__email {
    text-align: left;
  }
  &__link {
    color: $mine;
    opacity: 0.85;
    transition: .2s ease;
    @include hover() {
      opacity: 1;
    }
  }
}

.partners {
  box-shadow: 0px 4px 40px rgba(0, 0, 0, 0.05);
  &__holder {
    padding: 95px 0;
    @include response(1439px){
      padding: 75px 0;
    }
    @include response(1365px){
      padding: 55px 0;
    }
    @include response(1250px){
      padding: 45px 0;
    }
    @include response(1024px){
      padding: 35px 0;
    }
  }
  &__list {}
  &__items {
    padding: 0 10px;
  }
  &__link {
    min-height: 90px;
    max-height: 100px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    img {
      margin: 0 auto;
    }
  }
}

.inst {
  padding: 160px 0 140px;
  background: #fcfaf8;
  @include response(1679px){
    padding: 140px 0 120px;
  }
  @include response(1439px){
    padding: 120px 0;
  }
  @include response(1365px){
    padding: 100px 0;
  }
  @include response(1250px){
    padding: 75px 0;
  }
  @include response(1024px){
    padding: 55px 0;
  }
  @include response(767px){
    padding: 45px 0;
  }
  @include response(479px){
    padding: 35px 0;
  }
  &__container {
    .section-title {
      margin-bottom: 70px;
      @include response(1439px){
        margin-bottom: 55px;
      }
      @include response(1250px){
        margin-bottom: 35px;
      }
    }
  }
  &__holder {}
  &__list {
    margin: -10px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    @include response(767px){
      margin: -5px;
    }
  }
  &__items {
    width: 100%;
    margin: 10px;
    border-radius: 15px 15px 15px 0;
    overflow: hidden;
    max-width: calc(25% - 20px);
    flex-basis: calc(25% - 20px);
    @include response(767px){
      max-width: calc(25% - 10px);
      flex-basis: calc(25% - 10px);
      margin: 5px;
    }
    @include response(479px){
      max-width: calc(50% - 10px);
      flex-basis: calc(50% - 10px);
    }
  }
  &__pic {}
}