.not-found {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: calc(100vh - 350px);
  height: auto;
  &__container {
    margin-top: 100px;
    padding: 190px 0 170px;
    @include response(1679px){
      padding: 140px 0;
    }
    @include response(1439px){
      padding: 120px 0;
    }
    @include response(1365px){
      padding: 100px 0;
    }
    @include response(1250px){
      padding: 75px 0;
    }
    @include response(1024px){
      padding: 55px 0;
      margin-top: 80px;
    }
    @include response(767px){
      padding: 45px 0;
    }
    @include response(479px){
      padding: 35px 0;
    }
  }
  &__holder {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .button-holder {
      flex-basis: auto;
      max-width: 180px;
    }
  }
  &__block {
    margin-bottom: 75px;
    text-align: center;
    @include response(1679px){
      margin-bottom: 55px;
    }
    @include response(1439px){
      margin-bottom: 45px;
    }
    @include response(1365px){
      margin-bottom: 35px;
    }
  }
  &__pic {
    margin: 0 auto 35px;
    @include response(1250px){
      max-width: 90%;
    }
    @include response(1024px){
      max-width: 70%;
      margin: 0 auto 25px;
    }
    @include response(479px){
      max-width: 55%;
    }
  }
  &__desc {
    font-size: 22px;
    line-height: 28px;
    font-weight: 500;
    color: $chicago;
    text-align: center;
    @include response(479px){
      font-size: 18px;
      line-height: 22px;
    }
  }
}