.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  transition: all .2s ease;
  background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
  z-index: 900;
  &.active {
    background: $white;
    box-shadow: 0 4px 40px rgba(0, 0, 0, 0.05);
    .header__container {
      padding: 15px 0;
      @include response(1679px){
        padding: 15px;
      }
    }
    .productions__list {
      top: 50px;
      @include response(1250px){
        top: 46px;
      }
      @include response(479px){
        top: 30px;
      }
    }
    .lang__list {
      top: 50px;
      @include response(1250px){
        top: 46px;
      }
    }
    .navigation {
      @include response(1023px){
        top: 74px;
        height: calc(100vh - 74px);
      }
      @include response(767px){
        top: 69px;
        height: calc(100vh - 69px);
      }
    }
  }
  &__container {
    padding: 25px 0;
    display: flex;
    align-items: center;
    transition: all .2s ease;
    @include response(1679px){
      padding: 25px 15px;
    }
    @include response(1023px){
      padding: 15px 10px;
    }
  }
  &__holder {
    display: flex;
    justify-content: flex-end;
    width: 100%;
    max-width: 100%;
    align-items: center;
    @include response(1023px){
      justify-content: flex-end;
      margin-right: 65px;
    } 
  }
}

.navigation {
  max-width: 100%;
  width: 100%;
  margin-right: 65px;
  transition: .2s ease-in-out;
  @include response(1679px){
    margin-right: 52px;
  }
  @include response(1023px){
    position: fixed;
    left: 0;
    margin: 0;
    top: 74px;
    height: calc(100vh - 74px);
    background: rgba(0, 0, 0, 0.7);
    transform: translateX(-100%);
  }
  @include response(767px){
    top: 69px;
    height: calc(100vh - 69px);
  }
  &__holder {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    max-width: 100%;
    width: 100%;
    @include response(1023px){
      flex-direction: column;
      justify-content: flex-start;
      max-height: 100%;
      overflow: auto;
      height: 100%;
      width: 75%;
      background: $white;
      margin-right: auto;
      padding-top: 55px;
    }
    @include response(767px){
      padding-top: 35px;
    }
    @include response(479px){
      width: 100%;
    }
  }
  &.open-menu {
    transition: .2s ease-in-out;
    @include response(1023px){
      transform: translateX(0);
    }
  }
}

.logo {
  max-width: 154px;
  flex: 1 1 154px;
  width: 100%;
  margin-right: 68px;
  @include response(1250px){
    margin-right: 46px;
  }
  @include response(1023px){
    margin-right: auto;
  }
}

.productions {
  margin-right: auto;
  cursor: pointer;
  position: relative;
  &::before {
    content: '';
    display: block;
    width: 100%;
    height: 60px;
    position: absolute;
    top: 0;
    left: 0;
  }
  @include response(1023px){
    margin: 0 0 15px;
    order: 2;
  }
  @include hover() {
    .productions__title {
      color: $niagara;
      &::after {
        content: '';
        display: block;
        transform: translateY(-50%) rotate(180deg);
      }
    }
    .productions__list {
      opacity: 1;
      visibility: visible;
    }
  }
  &.active {
    .productions__list {
      opacity: 1;
      visibility: visible;
    }
  }
  &__holder {
  }
  &__title {
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    position: relative;
    transition: .2s ease-in-out;
    &::after {
      content: '';
      display: block;
      width: 9px;
      height: 6px;
      background: url(../images/icon-lang.svg) center/contain no-repeat;
      transform-origin: 50% 50%;
      position: absolute;
      right: -11px;
      top: 50%;
      transform: translateY(-50%) rotate(0);
      transition: .2s ease-in-out;
    }
    > a {
      color: $mine;
      &:hover{
        color: $niagara;
      }
    }
  }
  &__list {
    position: absolute;
    top: 100%;
    left: 0;
    background: $white;
    padding: 33px 15px 30px 27px;
    max-width: 250px;
    min-width: 250px;
    width: 100%;
    top: 60px;
    left: -27px;
    transform: translate(0, 0);
    border-radius: 0 0 50px;
    opacity: 0;
    visibility: hidden;
    transition: .3s ease-in-out;
    @include response(1023px){
      top: 30px;
      left: 50%;
      transform: translateX(-50%);
      padding: 15px;
      z-index: 3;
    }
    &.home {
      top: 20px;
    }
  }
  &__items {
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    transition: .3s ease-in-out;
    margin-bottom: 20px;
    @include response(1023px){
      text-align: center;
    }
    &:last-child {
      margin: 0;
    }
    &:hover{
      color: $niagara;
    }
    > a {
      color: #333;
      &:hover{
        color: $niagara;
      }
    }
  }
  &.home {
    .productions__list {
      top: 20px;
    }
  }
}

.nav {
  margin-right: 50px;
  @include response(1679px){
    margin-right: 42px;
  }
  @include response(1023px){
    margin: 0 0 25px;
    width: 100%;
    order: 1;
  }
  &__menu {
    display: flex;
    justify-content: center;
    align-items: center;
    @include response(1023px){
      flex-direction: column;
    }
    li {
      padding-right: 40px;
      @include response(1679px){
        padding-right: 36px;
      }
      @include response(1250px){
        padding-right: 28px;
      }
      @include response(1023px){
        padding: 10px;
        width: 100%;
        text-align: center;
      }
      &:last-child {
        padding: 0;
        @include response(1023px){
          padding: 10px;
        }
      }
      a {
        display: block;
        font-weight: 600;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.1em;
        color: $mine;
        text-transform: uppercase;
        padding: 5px;
        transition: .2s ease-in-out;
        @include response(1023px){
          padding: 0;
        }
        @include hover() {
          color: $niagara;
        }
      }
    }
  }
}

.lang {
  position: relative;
  padding: 0 5px;
  cursor: pointer;
  &::before {
    content: '';
    display: block;
    width: 100%;
    height: 60px;
    position: absolute;
    top: 0;
    left: 0;
  }
  @include response(1023px){
    order: 3;
  }
  @include hover() {
    .lang__list {
      opacity: 1;
      visibility: visible;
    }
    .lang__active {
      color: $niagara;
      &::after {
        content: '';
        display: block;
        transform: translateY(-50%) rotate(180deg);
      }
    }
  }
  &::after {
    content: '';
    display: block;
    box-shadow: 0 4px 40px rgba(0, 0, 0, 0.05);
    position: absolute;
    top: 0;
    left: 0;
  }
  &.active {
    .lang__list {
      opacity: 1;
      visibility: visible;
    }
  }
  &__active {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.1em;
    color: $mine;
    text-transform: uppercase;
    position: relative;
    transition: .2s ease-in-out;
    &::after {
      content: '';
      display: block;
      width: 9px;
      height: 6px;
      background: url(../images/icon-lang.svg) center/contain no-repeat;
      transform-origin: 50% 50%;
      position: absolute;
      right: -11px;
      top: 50%;
      transform: translateY(-50%) rotate(0);
      transition: .2s ease-in-out;
    }
  }
  &__list {
    position: absolute;
    top: 100%;
    padding: 0 5px;
    border-radius: 0 0 50px;
    background: $white;
    padding: 30px 15px;
    top: 60px;
    left: -25px;
    transform: translate(0, 0);
    opacity: 0;
    visibility: hidden;
    transition: .3s ease-in-out;
    @include response(1023px){
      padding: 10px;
      top: 24px;
      left: -20px;
    }
  }
  &__items {
    padding: 0 15px;
    margin-bottom: 20px;
    @include response(1023px){
      margin-bottom: 10px;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__link {
    display: block;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.1em;
    color: $mine;
    text-transform: uppercase;
    transition: .2s ease-in-out;
    &:hover {
      color: $niagara;
    }
  }
  &.home-lang {
    .lang__list {
      top: 20px;
      background: transparent;
    }
  }
}

.order {
  max-width: 184px;
  flex-basis: 184px;
  min-width: 184px;
  width: 100%;
  @include response(1250px){
    max-width: 160px;
    flex-basis: 160px;
    min-width: 160px;
  }
  @include response(1023px){
    max-width: 55px;
    flex-basis: 55px;
    min-width: 55px;
  }
  @include response(767px){
    max-width: 45px;
    flex-basis: 45px;
    min-width: 45px;
  }
  &__holder {
    .btn {
      justify-content: center;
      @include response(767px){
        padding: 12px;
        justify-content: center;
      }
    }
  }
  &__text {
    display: inline-block;
    margin-right: 14px;
    @include response(1023px){
      display: none;
    }
  }
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    @include response(767px){
      width: 15px;
      height: 15px;
    }
  }
}

.burger {
  cursor: pointer;
  display: none;
  position: fixed;
  right: 15px;
  top: 30px;
  @include response(1023px){
    display: block;
  }
  &__holder {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 35px;
    span {
      display: inline-block;
      width: 100%;
      height: 2px;
      background: $niagara;
      margin: 2px 0;
      transition: .3s ease-in-out;
    }
    .line-1 {
      width: 100%;
      transition-delay: .3s;
    }
    .line-2 {
      width: 75%;
      transition-delay: .2s;
    }
    .line-3 {
      width: 55%;
      transition-delay: .1s;
    }
  }
  &.active {
    .burger__holder {
      span {
        width: 100%;
      }
    }
  }
}