.delivery-page {
  margin-bottom: 150px;
  @include response(1439px){
    margin-bottom: 90px;
  }
  @include response(1365px){
    margin-bottom: 75px;
  }
  @include response(1023px){
    margin-bottom: 60px;
  }
  @include response(479px){
    margin-bottom: 45px;
  }
  &__container {
    max-width: 785px;
  }
  &__tabs {
    margin-bottom: 70px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.block-text {
  &__main {
    margin-bottom: 50px;
    @include response(767px){
      margin-bottom: 35px;
    }
    &:first-child {
      margin-bottom: 70px;
      @include response(767px){
        margin-bottom: 50px;
      }
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__title {
    margin: 0 0 30px;
    text-align: left;
  }
  ul {
    margin: 0 0 10px;
    color: $mine;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    li {
      position: relative;
      padding-left: 20px;
      &::after {
        content: '';
        display: block;
        height: 6px;
        width: 6px;
        border-radius: 50%;
        background: #08b87d;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
  > p {
    margin: 0 0 30px;
    @include response(767px){
      margin: 0 0 15px;
    }
    &:last-child {
      margin: 0;
    }
  }
  .point {
    position: relative;
    padding-left: 20px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    &::after {
      content: '';
      display: block;
      height: 6px;
      width: 6px;
      border-radius: 50%;
      background: $niagara;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}