.blog-page {
  margin-bottom: 150px;
  @include response(1439px){
    margin-bottom: 90px;
  }
  @include response(1365px){
    margin-bottom: 75px;
  }
  @include response(1023px){
    margin-bottom: 60px;
  }
  @include response(479px){
    margin-bottom: 45px;
  }
  &__container {}
  &__head {
    margin-bottom: 60px;
  }
  &__tabs {
    &-list {
      display: flex;
      justify-content: center;
      align-items: center;
      @include response(767px){
        flex-direction: column;
        align-items: flex-start;
      }
    }
    &-items {
      @include response(767px){
        margin-bottom: 25px;
        &:last-child {
          margin: 0;
        }
      }
    }
    &-link {
      font-weight: 600;
      font-size: 14px;
      line-height: 22px;
      text-align: center;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: $mine;
      padding: 0px 30px;
      position: relative;
      @include response(767px){
        padding: 0 30px 0 0;
      }
      &::after {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        right: 0;
        bottom: -10px;
        border-bottom: 2px solid #e0e0e0;
        transition: all .3s ease-in-out;
      }
      &.active {
        &::after {
          content: '';
          display: block;
          border-bottom: 2px solid $niagara;
        }
      }
    }
  }

  &__body {}
  &__list {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    margin: 0 -10px 85px;
    @include response(767px){
      margin: 0 -5px 65px;
    }
    @include response(479px){
      margin: 0 -5px 25px;
    }
  }
  &__items {
    max-width: 33.3333%;
    flex: 1 1 33.3333%;
    width: 100%;
    padding: 0 10px;
    margin-bottom: 50px;
    @include response(767px){
      padding: 0 5px;
      max-width: 50%;
      flex-basis: 50%;
    }
    @include response(479px){
      max-width: 100%;
      flex-basis: auto;
    }
    &:nth-last-child(-n+3) {
      margin: 0;
      @include response(767px){
        margin-bottom: 50px;
      }
    }
    .box-articles {
      max-width: 100%;
      flex-basis: auto;
      padding: 0; 
      &__body {
        min-height: 180px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        @include response(767px){
          min-height: 170px;
        }
        @include response(479px){
          min-height: auto;
        }
      }
      .button-holder {
        flex-basis: auto;
      }
    }
  }
}





.tabs-list {
  display: flex;
  justify-content: center;
  align-items: center;
}
.tabs-items {}
.tabs-link {
  font-weight: 600;
  font-size: 14px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: $mine;
  padding: 0px 30px;
  position: relative;
  &::after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    bottom: -10px;
    border-bottom: 2px solid #e0e0e0;
    transition: all .3s ease-in-out;
  }
  &.active {
    &::after {
      content: '';
      display: block;
      border-bottom: 2px solid $niagara;
    }
  }
}