.product-card {
  padding: 140px 0 150px;
  @include response(1023px){
    padding: 100px 0 70px;
  }
  @include response(767px){
    padding: 100px 0 55px;
  }
  @include response(479px){
    padding: 100px 0 35px;
  }
  &__container {
    max-width: 1270px;
  }
  &__holder {}
  &__section {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 80px;
    flex-direction: row;
    align-items: stretch;
    @include response(1023px){
      flex-direction: column-reverse;
      margin-bottom: 60px;
    }
    @include response(767px){
      margin-bottom: 40px;
    }
  }
  &__photo {
    max-width: 704px;
    min-width: 704px;
    width: 100%;
    margin-right: auto;
    @include response(1365px){
      max-width: 650px;
      min-width: 650px;
    }
    @include response(1250px){
      max-width: 580px;
      min-width: 580px;
      padding-right: 45px;
    }
    @include response(1023px){
      max-width: 100%;
      min-width: auto;
      padding: 0;
      text-align: center;
      margin-bottom: 0;
    }
  }
  &__content {
    margin-bottom: 12px;
    &-pic {
      border-radius: 15px;
      overflow: hidden;
    }
  }
  &__list {
    .tabs-list {
      margin: 0 -6px;
      @include response(767px){
        margin: 0 -3px;
      }
    }
    .tabs-items {
      padding: 0 6px;
      @include response(767px){
        padding: 0 3px;
      }
    }
    .tabs-link {
      padding: 0;
      &::after {
        content: '';
        display: block;
        border-bottom: none;
      }
    }
    &-pic {
      display: block;
      border-radius: 15px;
      overflow: hidden;
    }
  }
  &__meta {
    max-width: 470px;
    flex-basis: 470px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include response(1023px){
      max-width: 100%;
      flex-basis: auto;
      margin-bottom: 35px;
    }
  }
  &__breadcrumbs {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 34px;
    @include response(1023px){
      justify-content: center;
    }
    @include response(479px){
      margin-bottom: 26px;
    }
  }
  &__info {
    margin-bottom: 46px;
    @include response(767px){
      margin-bottom: 28px;
    }
  }
  &__title {
    margin-bottom: 16px;
  }
  &__desc {
    margin-bottom: 16px;
  }
  &__tags {
    margin-bottom: 36px;
  }
  &__table {
    margin-bottom: 32px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    @include response(1023px){
      max-width: 50%;
    }
    @include response(767px){
      max-width: 90%;
    }
    &-col {
      flex: 1 1 33.3333%;
      max-width: 33.3333%;
      width: 100%;
      text-align: left;
    }
    &-title {
      font-size: 16px;
      line-height: 24px;
      font-weight: 600;
      color: $mine;
      margin-bottom: 6px;
    }
    &-desc {}
  }
  &__size {
    &-title {
      font-size: 16px;
      line-height: 24px;
      font-weight: 600;
      color: $mine;
      margin-bottom: 14px;
    }
  }
  &__button {
    max-width: 230px;
    width: 100%;
    margin-right: auto;
    > .btn {
      justify-content: center;
      padding: 18px 10px;
    }
    .order__text {
      @include response(1023px){
        display: block;
      }
    }
  }
  &__body {}
}

.tags {
  &__list {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    margin: -5px;
  }
  &__items {
    border-radius: 20px;
    overflow: hidden;
    text-align: center;
    margin: 5px;
  }
  &__link {
    font-size: 12px;
    line-height: 16px;
    font-weight: 400;
    color: $chicago;
    background: #f9f4f0;
    padding: 8px 25px;
    transition: .2s ease;
    @include hover() {
      color: $white;
      background: $niagara;
    }
  }
}

.sizes {
  @include response(1023px){
    max-width: 45%;
  }
  @include response(767px){
    max-width: 90%;
  }
  &__list {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    margin: -6px -4px;
  }
  &__items {
    max-width: 100px;
    flex-basis: 100px;
    width: 100%;
    border-radius: 20px;
    overflow: hidden;
    position: relative;
    margin: 6px 4px;
  }
  &__label {
    display: block;
    border: 1px solid #cda97b;
    border-radius: 20px;
    width: 100%;
    text-align: center;
    padding: 7px;
    font-size: 12px;
    line-height: 16px;
    font-weight: 400;
    color: $mine;
    cursor: pointer;
    @include hover() {
      border: 1px solid $mine;
    }
  }
  &__radio {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    width: 0.1px;
    height: 0.1px;
    &:checked ~ .sizes__label {
      border: 1px solid $mine;
    }
  }
}

.card-content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  @include response(1023px){
    flex-direction: column;
  }
  &__holder {
    max-width: 700px;
    flex-basis: 700px;
    width: 100%;
    margin-right: auto;
    @include response(1250px){
      padding-right: 45px;
      flex-basis: auto;
    }
    @include response(1023px){
      margin: 0 auto 35px;
      padding: 0;
    }
  }
  &__aside {
    @include response(1023px){
      width: 100%;
    }
  }
  &__list {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 40px;
    >.tabs-list {
      @include response(479px){
        flex-wrap: wrap;
        justify-content: flex-start;
      }
      > .tabs-items {
        @include response(479px){
          margin-bottom: 35px;
          &:last-child {
            margin: 0;
          }
        }
      }
    }
  }
  &__block {
    margin-bottom: 50px;
    width: 100%;
    @include response(767px){
      margin-bottom: 35px;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__title {
    margin-bottom: 15px;
    > .text-md {
      font-weight: 600;
      color: $mine;
    }
  }
  &__desc {
    color: $mine;
    > p {
      margin: 0 0 30px;
      @include response(767px){
        margin: 0 0 15px;
      }
      &:last-child {
        margin: 0;
      }
    }
    ul {
      margin: 0 0 30px;
      li {
        text-align: left;
      }
    }
  }
}

.aside {
  &__title {
    margin-bottom: 30px;
    text-align: left;
  }
  &__holder {
    @include response(1023px){
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      margin: 0 -10px;
      flex-wrap: wrap;
    }
    .block-product {
      margin: 0 0 40px;
      @include response(1023px){
        max-width: 50%;
        flex-basis: auto;
        padding: 0 10px;
      }
      @include response(479px){
        max-width: 100%;
      }
      &:last-child {
        margin: 0;
      }
      &__preview {
        @include response(1023px){
          width: 100%;
        }
      }
      &__body {
        @include response(1023px){
          min-height: auto;
        }
        @include response(767px){
          padding: 5px;
        }
      }
      &__img {
        @include response(1023px){
          img {
            width: 100%;
          }
        }
      }
    }
  }
}

.specific {
  width: 100%;
  margin-bottom: 36px;
  &__head {
    font-size: 12px;
    line-height: 18px;
    font-weight: 700;
    color: #3d3d3d;
    padding: 6px 18px;
    text-align: left;
    background: #e6f8ec;
  }
  &__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;
    border-bottom: 1px solid #f0f1f6;
    &:last-child {
      border-bottom: none;
    }
  }
  &__td {
    max-width: 50%;
    flex: 1 1 50%;
    width: 100%;
    text-align: left;
    &:first-child {
      border-right: 1px solid #f0f1f6;
    }
    > span {
      display: block;
      padding: 5px 18px;
    }
  }
}

.brand {
  &__holder {
    text-align: left;
    p {
      margin: 0 0 30px;
      &:last-child {
        margin: 0;
      }
    }
  }
}