.design-page {
  &__bg {
    margin: 100px 0 0;
    @include response(1023px){
      margin: 80px 0 0;
    }
  }
  &__container {
    .charity__holder {
      box-shadow: none;
    }
    .charity__img {
      min-height: 732px;
      border-radius: 160px 0px 0px 15px;
    }
    .charity__body {
      background: $niagara;
      border-radius: 0 15px 160px 0;

    }
    .triggers {
      &__row {
        @include response(1024px){
          flex-direction: column;
        }
        &-reverse {
          @include response(1250px){
            padding-left: 50px;
          }
          @include response(1024px){
            padding: 0;
          }
          .triggers__pic {
            margin: 0 0 0 143px;
            @include response(1679px){
              margin: 0 0 0 120px;
            }
            @include response(1439px){
              margin: 0;
            }
          }
          .triggers__img {
            @include response(1439px){
              padding: 0 0px 0 50px;
            }
            @include response(1024px){
              padding: 0;
            }
          }
        }
      }
      &__pic {
        margin: 0 143px 0 0;
        @include response(1679px){
          margin: 0 120px 0 0;
        }
        @include response(1439px){
          margin: 0;
          max-width: 75%;
          flex-basis: auto;
          min-width: auto;
        }
        @include response(767px){
          min-width: auto; 
        }
        @include response(479px){
          max-width: 85%;
        }
      }
      &__img {
        @include response(1439px){
          padding: 0 50px 0 0;
        }
        @include response(1024px){
          padding: 0;
        }
      }
      &__body {
        @include response(1024px){
          flex-basis: auto;
          max-width: 70%;
        }
        @include response(479px){
          max-width: 90%;
        }
      }
    }
    .triggers__pic {
      .triggers__img {
      }
    }
