.create-design {
  background-color: #fcfaf8;
  padding: 150px 0;
  @include response(1679px){
    padding: 130px 0;
  }
  @include response(1439px){
    padding: 110px 0;
  }
  @include response(1365px){
    padding: 90px 0;
  }
  @include response(1250px){
    padding: 75px 0;
  }
  @include response(1023px){
    padding: 55px 0;
  }
  @include response(439px){
    padding: 35px 0;
  }
  &__container {}
  &__holder {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    @include response(1250px){
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
  &__col {
    width: 100%;
    &:first-child {
      max-width: 638px;
      flex-basis: 638px;
      @include response(1439px){
        max-width: 45%;
        flex-basis: auto;
        padding-right: 15px;
      }
      @include response(1250px){
        max-width: 100%;
        padding: 0;
      }
    }
    &:last-child {
      max-width: 714px;
      flex-basis: 714px;
      @include response(1439px){
        max-width: 55%;
        flex-basis: auto;
      }
      @include response(1250px){
        max-width: 100%;
      }
    }
  }
  &__inner {
  }
  &__body {
    margin-bottom: 80px;
    @include response(1439px){
      margin-bottom: 60px;
    }
    @include response(1250px){
      margin-bottom: 40px;
    }
  }
  &__title {
    margin-bottom: 34px;
    text-align: left;
  }
  &__desc {
    width: 100%;
    text-align: left;
    > p {
      margin: 0 0 30px;
      &:last-child {
        margin: 0;
      }
    }
  }
  &__pic {
    border-radius: 15px 15px 15px 150px;
    overflow: hidden;
    @include response(1250px){
      text-align: center;
    }
  }
}

.form {
  max-width: 714px;
  flex-basis: 714px;
  width: 100%;
  margin: 0 auto;
  &__holder {}
  &__section {
    margin-bottom: 50px;
    @include response(479px){
      margin-bottom: 35px;
    }
    &:last-child {
      margin: 0;
    }
  }
  &__title {
    margin-bottom: 20px;
    font-size: 24px;
    line-height: 36px;
    font-weight: bold;
    color: $mine;
    text-align: left;
    @include response(479px){
      font-size: 20px;
      line-height: 26px;
    }
  }
  &__box {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    transition: .3s ease-in-out;
    &--comment {
      margin-bottom: 24px;
      height: 150px;
      opacity: 1;
      visibility: visible;
    }
    &.toggle-comment {
      height: 0;
      margin: 0;
      transition: .3s ease-in-out;
      opacity: 0;
      visibility: hidden;
    }
  }
  &__body {}
  &__row {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 0 -8px 20px;
    @include response(479px){
      flex-direction: column;
      margin: 0 0 20px;
    }
    &:last-child {
      margin: 0 -8px 0;
      @include response(479px){
        margin: 0;
      }
    }
  }
  &__cell {
    max-width: 348px;
    flex-basis: 348px;
    width: 100%;
    margin: 0 8px;
    @include response(479px){
      max-width: 100%;
      flex-basis: auto;
      margin: 0 0 15px;
    }
    &--lg {
      max-width: 100%;
      flex-basis: auto;
    }
  }
  &__lb {
    margin-bottom: 8px;
    display: block;
    text-align: left;
  }
  &__button {
    max-width: 185px;
    width: 100%;
    margin-right: auto;
    > .btn {
      justify-content: center;
    }
  }
  &__add {
    margin-bottom: 60px;
    @include response(767px){
      margin-bottom: 40px;
    }
    @include response(479px){
      margin-bottom: 30px;
    }
    &-holder {}
    &-lb {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      position: relative;
      @include hover() {
        .form__add-icon {
          border: 1px solid $white;
        }
      }
    }
    &-checkbox {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      visibility: hidden;
      width: 0.1px;
      height: 0.1px;
      &:checked ~ .form__add-icon {
        &::after {
          content: '';
          display: block;
          transform: translate(-50%, -50%) rotate(-90deg);
        }
      }
    }
    &-icon {
      height: 24px;
      width: 24px;
      border-radius: 50%;
      background: #cda97b;
      margin-right: 9px;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      transition: .3s ease-in-out;
      border: 1px solid transparent;
      &::after,
      &::before {
        content: '';
        display: block;
        height: 10px;
        width: 2px;
        background: $white;
        border-radius: 5px;
        position: absolute;
        cursor: pointer;
        transition: .5s ease-in-out;
        top: 50%;
        left: 50%;
      }
      &::before {
        transform: translate(-50%, -50%) rotate(0);
        content: '';
        display: block;
        height: 2px;
        width: 10px;
      }
      &::after {
        content: '';
        display: block;
        transform: translate(-50%, -50%) rotate(0);
      }
    }
    &-comment {
      display: inline-block;
      cursor: pointer;
    }
  }
}

.attach-file {
  border: 1px dashed $chicago;
  border-radius: 8px;
  position: relative;
  overflow: hidden;
  flex-direction: row;
  padding: 15px 30px;
  height: 70px;
  justify-content: flex-start;
  align-items: center;
  cursor: pointer;
  width: 100%;
  @include response(767px){
    height: 62px;
  }
  &.attached {
    background-color: #e6f8ec;
    border: 1px dashed $mine;
    .attach-file__del {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .attach-file__icon {
      display: none;
    }
  }
  .field-control {
    border: none;
    padding: 0;
    &:focus {
      border: none;
    }
  }
  &__inp {
    position: absolute;
    z-index: 30;
    top: 0;
    left: 0;
    opacity: 0;
    cursor: pointer;
  }
  &__val {
    display: block;
    font-size: 14px;
    line-height: 18px;
    font-weight: 500;
    color: #828282;
    opacity: 0.6;
    text-align: left;
    margin-right: 30px;
    text-align: center;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  &__icon {
    margin-right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__del {
    display: none;
    position: absolute;
    right: 30px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    z-index: 30;
    height: 100%;
  }
}
.select-field {
  max-width: 348px;
  flex-basis: 348px;
  width: 100%;
  @include response(479px){
    max-width: 100%;
    flex-basis: auto;
  }
  .select {
    width: 100%;
    border: 1px solid $chicago;
    border-radius: 8px;
    padding: 24px 20px;
    background: $white;
    @include response(767px){
      padding: 20px;
    }
    &--open {
      border: 1px solid $niagara;
      border-radius: 8px 8px 0 0;
      border-bottom-color: transparent;
      border-bottom-color: transparent;
      .select__list {
        border: 1px solid $niagara;
        border-top-color: transparent;
      }
    }
    &__current {
      font-size: 14px;
      line-height: 20px;
      font-weight: 500;
      color: #828282;
      opacity: 0.6;
    }
    &__item {
      color: $mine;
      opacity: 1;
      @include hover() {
        background: transparent;
        color: $niagara;
      }
      &--disabled {
        display: block;
        opacity: 0.3;
        @include hover() {
          color: $mine;
        }
      }
    }
    &__list {
      margin-top: 0;
      border-radius: 0 0 8px 8px;
    }
  }
}


.field-control {
  display: inline-block;
  vertical-align: top;
  font-family: 'Poppins', sans-serif;
  width: 100%;
  height: 70px;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
  padding: 20px 30px;
  color: $mine;
  background-color: $white;
  border: 1px solid $chicago;
  outline: none;
  resize: none;
  margin: 0;
  border-radius: 8px;
  transition: all .2s;
  @include response(767px){
    height: 62px;
  }
  &:focus {
    border: 1px solid $niagara;
  }
  &-area {
    height: 120px;
  }
  &-error {
    border: 1px solid #eb5757;
  }
  &-disabled {
    cursor: default;
  }
}
*::-webkit-input-placeholder {
  color: #828282;
  opacity: 0.6; 
}
*:-moz-placeholder {
  color: #828282;
  opacity: 0.6; 
}
*::-moz-placeholder {
  color: #828282;
  opacity: 0.6; 
}
*:-ms-input-placeholder {
  color: #828282;
  opacity: 0.6; 
}


.error-form {
  display: inline-block;
  text-align: left;
  color: #eb5757;
  font-size: 12px;
  line-height: 16px;
  font-weight: 500;
}
.hidden {
  display: none;
}