.contacts-page {
  &__container {
    margin-bottom: 150px;
    @include response(1439px){
      margin-bottom: 90px;
    }
    @include response(1365px){
      margin-bottom: 75px;
    }
    @include response(1023px){
      margin-bottom: 60px;
    }
    @include response(479px){
      margin-bottom: 45px;
    }
  }
  &__holder {}
  &__title {
    margin-bottom: 60px;
    @include response(767px){
      margin-bottom: 48px;
    }
    @include response(479px){
      margin-bottom: 36px;
    }
  }
  &__list {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    > .team {
      padding: 0 10px;
      max-width: 33.333%;
      flex-basis: 33.333%;
      @include response(767px){
        max-width: 50%;
        flex-basis: 50%;
        margin-bottom: 35px;
      }
      @include response(680px){
        max-width: 100%;
        flex-basis: auto;
      }
      &__holder {
        @include response(1250px){
          min-height: 450px;
        }
      }
      &__inner {
        @include response(767px){
          padding: 15px;
        }
      }
      &__info {
        @include response(1250px){
          top: 75%;
        }
        @include response(1023px){
          top: 45%;
        }
      }
    }
    .team__info {
      @include response(680px){
        top: 46%;
      }
    }
  }
}

.agent {
  padding: 147px 0 137px;
  background: rgba(249, 244, 240, 0.45);
  @include response(1439px){
    padding: 120px 0;
  }
  @include response(1365px){
    padding: 100px 0;
  }
  @include response(1250px){
    padding: 75px 0;
  }
  @include response(1023px){
    padding: 55px 0;
  }
  @include response(767px){
    padding: 35px 0;
  }
  &__title {
    margin-bottom: 70px;
    text-align: left;
    @include response(1439px){
      margin-bottom: 55px;
    }
    @include response(1250px){
      margin-bottom: 35px;
    }
  }
}

.info-contact {
  max-width: 300px;
  width: 100%;
  margin: 0 auto;
  &__holder {}
  &__title {
    margin-bottom: 20px;
    .title {
      font-size: 16px;
      line-height: 24px;
      font-weight: 600;
      text-align: left;
    }
  }
  &__main {}
  &__body {
    margin-bottom: 32px;
    &:last-child {
      margin: 0;
    }
  }
  &__subtitle {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 12px;
  }
  &__icon {
    margin-right: 10px;
  }
  &__label {
    color: $chicago;
  }
  &__info {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
  &__link {
    display: inline-block;
    color: $mine;
    text-align: left;
  }
}

.map {
  display: flex;
  justify-content: space-between;
  background: $white;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0px 4px 40px rgba(88, 85, 84, 0.1);
  @include response(1439px){
    max-width: 95%;
    width: 100%;
    margin: 0 auto;
  }
  @include response(1023px){
    flex-direction: column;
  }
  @include response(767px){
    max-width: 100%;
  }
  &__box {
    max-width: 408px;
    width: 100%;
    @include response(1365px){
      max-width: 365px;
    }
    @include response(1023px){
      max-width: 100%;
    }
  }
  &__nav {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 32px;
    @include response(767px){
      margin-bottom: 16px;
    }
    &-item {
      font-size: 14px;
      line-height: 20px;
      font-weight: 600;
      text-align: center;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: $mine;
      background: $white;
      cursor: pointer;
      flex: 1 1 50%;
      padding: 20px;
      transition: .3s ease;
      @include response(767px){
        padding: 15px;
      }
      @include hover() {
        background: $niagara;
        color: $white;
      }
      &.active {
        background: $niagara;
        color: $white;
      }
    }

  }
  &__info {
    max-width: 410px;
    width: 100%;
    align-self: stretch;
    &-nav {
    }
    &-content {}

  }
  &__list {
    &-inner {
      max-width: 275px;
      width: 100%;
      margin: 0 auto;
      @include response(1365px){
        max-width: 70%;
      }
      @include response(767px){
        max-width: 90%;
      }
    }
  }
}

#map {
  max-width: 1050px;
  width: 100%;
  height: 658px;
  @include response(1365px){
    height: 600px;
  }
  @include response(767px){
    height: 500px;
  }
  @include response(479px){
    height: 400px;
  }
}

.map-popup {
  &__title {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    text-align: left;
    color: $mine;
    margin-bottom: 12px;
  }
  &__name {
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    color: $chicago;
    text-align: left;
  }
  &__tel {
    font-size: 12px;
    line-height: 16px;
    font-weight: 500;
    color: $mine;
    text-align: left;
    display: inline-block;
  }
}

.map-item {
  &__head {}
  &__title {
    margin-bottom: 10px;
    &-name {
      font-size: 16px;
      line-height: 24px;
      font-weight: 600;
      text-align: left;
      color: $mine;
    }
  }
  &__cell {
    margin-bottom: 34px;
    @include response(767px){
      margin-bottom: 22px;
    }
  }
  &__logo {}
  &__body {}
  &__row {
    margin-bottom: 8px;
    &-title {
      > svg {
        margin-right: 10px;
      }
      font-size: 14px;
      line-height: 22px;
      font-weight: 500;
      color: $chicago;

    }
  }
  &__value {
    display: inline-block;
    color: $mine;
    text-align: left;
    font-size: 14px;
    line-height: 22px;
    font-weight: 500;
    font-style: normal;
  }
}