.breadcrumbs {
  margin-bottom: 8px;
  &__holder {}
  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }
  &__items {}
  &__link {
    color: $niagara;
    margin-right: 38px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: relative;
    opacity: 0.8;
    transition: opacity .2s ease;
    @include hover() {
      opacity: 1;
    }
    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 16px;
      height: 2px;
      border-radius: 5px;
      background: $black;
      right: 0;
      top: 50%;
      transform: translate(28px, -50%);
    }
  }
  &__current {

  }
}